
.vimeography-thumbnail-container {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  grid-column-gap: 40px;
  grid-row-gap: 50px;
  justify-content: center;
  margin-bottom: 2rem;
}

/* IE11 Support */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .vimeography-thumbnail-container {
    display: flex;
    flex-wrap: wrap;
  }
}
